@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=IBM+Plex+Mono:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&family=Raleway:ital,wght@0,500;1,500&display=swap');

* {
  margin: 0;
  font-family: monospace, serif;
  color: white;

}

body {
  background: black;
  min-height: 100vh;
}
.navbar {
  padding: 30px;
  display: flex;
  align-items: center;
  max-width: 80%;
  margin: 0 auto;
  border-bottom: 1px solid white;
  background-color: transparent;
}


.navbar h1 {
  color: white;
}
.navbar .links {
  margin-left: auto;
}

.navbar a {
  color: white;
  text-decoration: none;
  padding: 6px;
  font-family: 'Raleway';
  letter-spacing: 4px;
  
}

.navLink {
  margin: 20px;
}

.navbar a:hover { 
  color:grey;
}

.content {
  max-width: 800px;
  margin: 40px auto;
  padding: 20px;
}

/* Project formatting */

.project-preview {
  padding: 10px 16px;
  margin: 20px 0;
  border-bottom: 1px solid white;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
  
}

.about-me {
  padding: 10px 16px;
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
  margin-top:100vh;
  font-style: italic;
  text-align: center;
  
}

.project-preview-container {
  padding: 20px;
  
}

.project-preview:hover {
  box-shadow: 1px 3px 5px black;
}

.project-preview h2 {
  font-size: 20px;
  font-weight: 200;
  color: white;
  margin-bottom: 8px;
}

.project-preview a {
  text-decoration: none;
}

.project-details {
  background: inherit;
}

.project-details h2 {
  font-size: 20px;
  margin-bottom: 10px;
}


.project-details div {
  margin: 0;
 
}

.title-screen {
  
  position:absolute;
  top:0;
  left:0;
  right:0;
  width:100%;
  height:100vh;
  display:flex;
  justify-content: center;
  align-items:center;
  z-index: 1;
  border-bottom: 1px solid rgb(187, 192, 255);
  border: 20px solid white;
  box-sizing: border-box;
  text-align: center;
  
}

